@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;1000&display=swap');
.App {
  text-align: center;
}
.logo{
  width: 80px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
  border-radius: 10px;
}
.head-text h1 {
  position: relative;
  background: linear-gradient(140deg, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 13%, rgba(0, 212, 255, 1) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-family: 'Nunito', sans-serif;
  font-weight: 1000;
  font-size: 5em;
  transition: font-size 0.5s ease;
}
.result{
  font-size: 0.8em;
}
.initial-text{
  font-size: 5em;
  background: linear-gradient(140deg, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 13%, rgba(0, 212, 255, 1) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: bold;
  font-family: 'Nunito', sans-serif;
}
.read{
  padding: 5px;
  border: 1px solid #80B3FF;
  color: #80B3FF;
  position: relative;
  padding-right: 25px;
}
.read .bi-arrow-right-short{
  position: absolute;
  right: 10px;
  transition: right .5s ease;
}
.read:hover{
  background: #687EFF;
  border-color: #687EFF;
  color: white;
}
.read:hover .bi-arrow-right-short{
  right: 7px;
}
.result strong{
  font-size: 1.2em;
  background: linear-gradient(140deg, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 13%, rgba(0, 212, 255, 1) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-family: 'Nunito', sans-serif;
}
.input{
  position: relative;
  width: 70%;
  border-radius: 10px;
  transition: width 0.5s ease;
}
.input input{
  border-radius: 10px;
  width: 100%;
  height: 50px;
  border: none;
  outline: none;
  padding-right: 120px!important;
  overflow: hidden;
}
.input .searchbtn{
  position: absolute;
  top: 50%;
  border: none;
  outline: none;
  background: #80B3FF;
  transition: background-color .5s ease;
  padding: 5px 30px 5px 20px;
  border-radius: 10px;
  color: white;
  right: 10px;
  transform: translateY(-50%);
}
.searchbtn .bi-arrow-right-short{
  position: absolute;
  right: 10px;
  transition: right .5s ease;
}
.searchbtn:hover{
   background: #687EFF;
}
.searchbtn:hover .bi-arrow-right-short{
  right: 7px;
}

@media screen and (max-width: 460px) {
  .head-text h1 {
    line-height: 70px;
    padding: 10px 0;
    font-size: 3.5em;
  }
  .input{
    width: 100%;
  }
}
@media screen and (max-width: 344px) {
  .head-text h1 {
    font-size: 2.5em;
  }
}
/* Loader code */
.container1 {
  display: flex;
  justify-content: space-between;
  width: 250px;
  .circle {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: linear-gradient(45deg,
      rgba(2,0,36,1) 0%,
      rgba(9,9,121,1) 35%,
      rgba(0,212,255,1) 100%);
    box-shadow: inset 0 0 0 5px rgba(255,255,255,0.3);
    transform: translateX(0);
    z-index: 2;
    &:nth-child(1) {
      animation: move-1 2s infinite;
    }
    &:nth-child(3) {
      animation: move-3 2s infinite;
    }
  }
}
@keyframes move-1 {
  0%   {z-index: 3; transform: translateX(0);}
  25%  {z-index: 3; transform: translateX(80px);}
  50%  {z-index: 3; transform: translateX(0);}
  
  50.1%{z-index: 1; transform: translateX(0);}
  75%  {z-index: 1; transform: translateX(80px);}
  100% {z-index: 1; transform: translateX(0);}
}
@keyframes move-3 {
  0%   {z-index: 1; transform: translateX(0);}
  25%  {z-index: 1; transform: translateX(-80px);}
  50%  {z-index: 1; transform: translateX(0);}
  
  50.1%{z-index: 3; transform: translateX(0);}
  75%  {z-index: 3; transform: translateX(-80px);}
  100% {z-index: 3; transform: translateX(0);}
}